<template>
  <b-form @submit="submit">
    <b-card class="searchbox">
    <b-col sm="12" class="px-1">
      <b-form-group>
        <label for="zipcode_input">Zip code:</label>
        <b-form-input
          autoComplete="off"
          class="active"
          type="text"
          id="zipcode_input"
          v-model="address.zipCode"
          v-debounce:600ms="search"
        ></b-form-input>
      </b-form-group>
    </b-col>
    <b-col cols="12" class="px-1">
      <b-form-group>
        <label for="city_input">City:</label>
        <b-form-input
          autoComplete="off"
          type="text"
          id="city_input"
          v-debounce:600ms="search"
          v-model="address.city"
        ></b-form-input>
      </b-form-group>
    </b-col>
    </b-card>
  </b-form>
</template>

<script>
import ApiService from '../utils/ApiService'

export default {
  name: 'SearchBox',
  props: ['geoLocation'],
  data () {
    return {
      searchResult: [],
      address: {
        zipCode: '',
        city: ''
      }
    }
  },
  computed: {
    isAddressValid () {
      return this.searchResult ? Boolean(this.searchResult.length) : false
    },
    isAddressFilled () {
      return Object.values(this.address).some(entry => !!entry)
    }
  },
  watch: {
    geoLocation (val) {
      if (val) this.getAddressFromGeoLocation(val)
    },
    isAddressValid (val) {
      if (!val) this.$emit('setSearchProgress', 0)
    },
  },
  created () {
  },
  methods: {
    search () {
      if (this.isAddressFilled) {
        this.searchByAddress(this.address)
      }
    },
    submit () {
    },
    async searchByAddress (address) {
      this.$emit('setSearchProgress', 0)
      // TODO: add params to query in a better way
      let url = `https://nominatim.openstreetmap.org/search.php?format=jsonv2`
      if (address.zipCode) {
        url += `&postalcode=${address.zipCode}`
      }
      if (address.city) {
        url += `&city=${address.city}`
      }
      const response = await new ApiService(url).get()
      this.searchResult = response.data
      this.$emit('setSearchProgress', 100)
      this.$emit('searchResult', response.data)
    },
    async getAddressFromGeoLocation (geoLocation) {
      let url = `https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${geoLocation.lat}&lon=${geoLocation.lng}`
      const response = await new ApiService(url).get()
      this.address.zipCode = response.data.address.postcode
      this.address.city = response.data.address.administrative
      this.$emit('searchResult', [response.data])
    }
  }
}
</script>
